import React from 'react';
import axios from 'axios';
import Movie from './Movie';

class App extends React.Component{

  //statesspsp is data! object!
  state = {
    isLoading: true,
    movies: []
  };

  getMoivies = async () => {
    const {data: {data : {movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json");
    this.setState({movies: movies, isLoading: false});
  }

  componentDidMount(){
    console.log("componentDidMount");
    this.getMoivies();
  }

  render(){
    const { isLoaing, movies} = this.state;
    return  <div className="movies">
              {isLoaing? "Loading..." : movies.map(movie => {
                console.log(movie);
                return <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres} />
              }) }
            </div>

  }
}

export default App;
